import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
import md5 from '@/common/md5.js'
export default {
	data() {
		return {
			userId: "",
			password: "",
			repassword: ""
		}
	},
	methods: {
		register(){
			let _this = this
			var registerRule = [{
					name: 'userId',
					checkType: 'notnull',
					checkRule: '',
					errorMsg: '请输入账号'
				},
				{
					name: 'password',
					checkType: 'notnull',
					errorMsg: '请输入登录密码'
				},
				{
					name: 'password',
					checkType: 'string',
					checkRule: '6,32',
					errorMsg: '密码最为 6-32 个字符'
				},
				{
					name: 'repassword',
					checkType: 'same',
					checkRule: _this.password,
					errorMsg: '两次输入密码不一致,请检查'
				}
			];
			var checkRes = graceChecker.check(_this, registerRule);
			if (checkRes) {
				uni.request({
				    url: this.Server_IP + 'register', //仅为示例，并非真实接口地址。
				    data: {
				        userId: this.userId,
						password: md5(this.password)
				    },
				    header: {
				        'custom-header': 'register' //自定义请求头信息
				    },
					method:"POST",
					dataType:"json",
				    success: (res) => {
				        console.log(res.data);
						if(res.data.info.code == '0'){
							uni.setStorageSync('userId', res.data.data.userId);
							console.log("成功")
							uni.navigateBack({
							    delta: 1
							});
						}else{
							console.log(res.data.info.message);
							uni.showToast({
								icon: 'none',
								title: res.data.info.message
							});
						}
				    },
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
		}
	}
}
